import React from "react";
import Alert from "react-bootstrap/Alert";
import Button from "react-bootstrap/Button";

export default function Cart(props) {
  const { cartItems, add, remove, buy } = props;

  const totalPrice = cartItems.reduce(
    (sum, product) => sum + product.price * product.number,
    0
  );

  const priceTr = cartItems.reduce((sum, product) => totalPrice - 150, 0);

  function Header({ disabled }) {
    if (disabled) {
      return null; //  null,    
    }

    return <div></div>;
  }

  return (
    <div className="white">
      <Alert variant="success">
        <Alert.Heading></Alert.Heading>
        {cartItems.length === 0 && <p> </p>}
        <table>
          <tr>
            <td width="25%"></td>
            <td width="33%"></td>
            <td width="25%"></td>
            <td width="25%"></td>
          </tr>
          {cartItems.map((product) => (
            <tr>
              <td width="25%">{product.name}</td>
              <td width="25%">
                <Button onClick={() => remove(product)} variant="danger">
                  -
                </Button>
                {product.number}
                <Button onClick={() => add(product)} variant="success">
                  +
                </Button>
              </td>
              <td width="25%">{product.price}</td>
              <td width="25%">{product.price * product.number}</td>
            </tr>
          ))}
        </table>
        <hr />
        <p className="mb-0">
          :{totalPrice}  :{priceTr}
        </p>
        <Header priceTr={priceTr} />
        {/*    Cart  */}
        <Button
          onClick={buy}
          variant="success"
          disabled={cartItems.length === 0}
        >
           
        </Button>
      </Alert>
    </div>
  );
}
